<template>
  <v-dialog v-model="dialog" persistent max-width="600px">
    <template v-slot:activator="{ on, attrs }">
      <!-- build button for edit camping  -->
      <v-btn
        class="ma-2"
        outlined
        small
        fab
        v-bind="attrs"
        v-on="on"
        color="primary"
      >
        <v-icon>mdi-pencil-outline</v-icon>
      </v-btn>
      <!-- end  build button for add camping  -->
    </template>
    <v-form ref="form" lazy-validation v-model="valid">
      <v-card>
        <!-- title card dialog  -->
        <v-card-title>
          <v-row>
            <v-col>
              <span class="text-h5">Edit Camping</span>
            </v-col>
            <v-col>
              <v-switch
                v-model="campingStatus"
                inset
                :label="`Camping is : ` + campingStateText"
              ></v-switch>
            </v-col>
          </v-row>
        </v-card-title>
        <!--  -->
        <v-card-text>
          <v-container>
            <v-row>
              <!-- Name field card  -->
              <v-col cols="12">
                <v-text-field
                  :rules="nameRules"
                  label="camping Name Ar*"
                  required
                  v-model="camping.title.ar"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  :rules="nameRules"
                  label="camping Name En *"
                  required
                  v-model="camping.title.en"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  :rules="nameRules"
                  label="camping Name Tr *"
                  required
                  v-model="camping.title.tr"
                ></v-text-field>
              </v-col>
              <!--  -->

              <!-- Description field card  -->
              <v-col cols="12">
                <v-textarea
                  label="camping Description Ar*"
                  v-model="camping.description.ar"
                  :rules="descriptionRules"
                >
                </v-textarea>
              </v-col>

              <v-col cols="12">
                <v-textarea
                  label="camping Description En*"
                  v-model="camping.description.en"
                  :rules="descriptionRules"
                >
                </v-textarea>
              </v-col>
              <v-col cols="12">
                <v-textarea
                  label="camping Description Tr *"
                  v-model="camping.description.tr"
                  :rules="descriptionRules"
                >
                </v-textarea>
              </v-col>
              <!--  -->

              <!-- total amount  field card  -->
              <v-col cols="12">
                <v-text-field
                  prepend-inner-icon="mdi-currency-usd"
                  label="Total Amount *"
                  v-model="camping.total_amount"
                  :rules="totalAmountRules"
                >
                </v-text-field>
              </v-col>
              <!--  -->

              <!--  build  selection field from section-->
              <v-col cols="12">
                <v-autocomplete
                  v-model="camping.section_id"
                  label="sections "
                  :items="
                    allSections.map((item) => {
                      return {
                        name: item.name[lang],
                        id: item.id,
                      };
                    })
                  "
                  item-text="name"
                  item-value="id"
                >
                  <template v-slot:selection="data">
                    <v-chip
                      class="secondary"
                      v-bind="data.attrs"
                      :input-value="data.title"
                      close
                      @click="data.select"
                      @click:close="removeSection(data.item)"
                    >
                      {{ data.item.name }}
                    </v-chip>
                  </template>
                </v-autocomplete>
              </v-col>
              <!-- end build  selection field from section-->

              <!--  build  selection field from project-->
              <v-col cols="12">
                <v-autocomplete
                  v-model="camping.project_id"
                  label="projects "
                  :items="
                    allProjects.map((item) => {
                      return {
                        title: item.title[lang],
                        id: item.id,
                      };
                    })
                  "
                  item-text="title"
                  item-value="id"
                >
                  <template v-slot:selection="data">
                    <v-chip
                      class="primary"
                      v-bind="data.attrs"
                      :input-value="data.title"
                      close
                      @click="data.select"
                      @click:close="remove(data.item)"
                    >
                      {{ data.item.title }}
                    </v-chip>
                  </template>
                </v-autocomplete>
              </v-col>
              <!-- end build  selection field from project-->

              <!-- build nation field  -->
              <build-nation-field
                v-on:changNation="changNation"
                :nation="camping.nation_id"
              />
              <!-- end build nation field  -->

              <v-hover v-slot="{ hover }" open-delay="1000">
                <v-col cols="12">
                  <v-text-field
                    label="Camping Video"
                    required
                    v-model="camping.vedio_url"
                  ></v-text-field>

                  <v-container
                    v-html="camping.vedio_url"
                    v-if="camping.vedio_url && hover"
                    class="video"
                  >
                  </v-container>
                </v-col>
              </v-hover>

              <!-- build image field  -->
              <v-col cols="12">
                <v-file-input
                  v-model="file"
                  chips
                  accept="image/png, image/jpeg"
                  label="Image"
                  @change="onFileChange"
              /></v-col>
              <v-col cols="12">
                <v-img
                  contain
                  max-height="250"
                  aspect-ratio="1"
                  :lazy-src="image ? image : minUrlImage + camping.image_url"
                  :src="image ? image : minUrlImage + camping.image_url"
                >
                </v-img>
              </v-col>
            </v-row>
          </v-container>
          <small color="red">*indicates required field</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="dialog = false"> Cancel </v-btn>
          <v-btn
            color="secondary"
            class="add-camping-button"
            text
            @click="updateCamping"
          >
            <span v-if="!progressEditCamping">Update Camping</span>
            <v-progress-circular
              v-if="progressEditCamping"
              color="primary"
              indeterminate
            ></v-progress-circular>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-form>
  </v-dialog>
</template>
<script>
import { campingController } from "./camping";
export default {
  props: ["campingData"],
  computed: {
    lang() {
      return this.$store.getters.lang;
    },
  },
  data() {
    return {
      // form
      valid: true,
      // validation form
      nameRules: [(v) => !!v || "Name is required"],
      descriptionRules: [(v) => !!v || "Description is required"],
      totalAmountRules: [(v) => this.isNumeric(v) || "type number valed "],
      campingStatus: this.campingData.status,
      campingStateText: "An Active",

      // image
      file: null,
      image: null,
      camping: this.campingData,
      minUrlImage: this.$store.state.imageUlr + "camping/",
      //
      progressEditCamping: false,
      isUpdating: false,
      dialog: false,
      allProjects: this.$store.getters.projects,
      allSections: this.$store.getters.sections,
    };
  },

  watch: {
    isUpdating(val) {
      if (val) {
        setTimeout(() => (this.isUpdating = false), 3000);
      }
    },
    campingStatus(state) {
      this.camping.status = state;
      if (state) {
        this.campingStateText = "Active";
      } else {
        this.campingStateText = "An Active";
      }
    },
    dialog(val) {
      if (val) {
        console.log(this.campingData);
        this.allProjects = this.$store.getters.projects;
      }
    },
  },
  methods: {
    isNumeric: function (n) {
      return !isNaN(parseFloat(n)) && isFinite(n);
    },

    changNation(nation) {
      this.camping.nation_id = nation;
    },
    removeSection() {
      this.camping.section_id = null;
    },
    remove() {
      this.camping.project_id = null;
    },
    updateCamping: async function () {
      if (this.$refs.form.validate()) {
        this.progressEditCamping = true;
        var state = await campingController.updateCamping(
          this.file,
          this.camping
        );
        if (state === true) {
          this.dialog = false;
          this.$emit("getVal");
        } else {
          this.progressEditCamping = false;
        }
        this.progressEditCamping = false;
      }
    },

    onFileChange(e) {
      if (e != null) {
        const reader = new FileReader();
        reader.readAsDataURL(this.file);
        reader.onload = (e) => {
          this.image = e.target.result;
        };
      } else {
        this.file = null;
        this.image = null;
      }
    },

    restData() {
      this.dialog = false;
      this.image = null;
      this.$refs.form.reset();
      this.file = null;
      this.progressEditCamping = false;
    },
  },
  components: {
    "build-nation-field": () => import("../nation.vue"),
  },
};
</script>
